import dices from '../../components/dices.vue'
export default {
	components: {
		dices
	},
	data() {
		return {
			pointsArr: [1, 6, 3, 4, 1, 2],
			count: 0,
			capOpen: false,
			lock: false,
			shaking: false,
			innerAudioContext: null
		};
	},
	mounted() {
		this.innerAudioContext = uni.createInnerAudioContext();
		this.innerAudioContext.autoplay = false;
		this.innerAudioContext.src = 'https://hkb.weishuapay.com/update/1683.wav'
		this.innerAudioContext.onPlay(() => {
		  console.log('开始播放');
		});
		this.innerAudioContext.onError((res) => {
		  console.log(res.errMsg);
		  console.log(res.errCode);
		});
	},
	methods: {
		random(min, max) {
			return Math.floor(Math.random() * (max - min + 1)) + min;
		},
		shake() {
			if(this.lock) {
				return
			}
			this.shaking = true
			wx.vibrateLong({
				success() {
					console.log('vibration');
				}
			})
			this.innerAudioContext.play()
			this.count = 0
			this.capOpen = false
			this.pointsArr = []
			setTimeout(() => {
				for(let i=0; i<6; i++) {
					this.pointsArr.push(this.random(1, 6))
				}
			}, 300)
			setTimeout(() => {
				this.capOpen = true
				this.shaking = false
				this.sum()
			}, 1500)
		},
		sum() {
			this.count = this.pointsArr.reduce((a, b) => a + b)
		},
		lockToggle() {
			this.lock = !this.lock
		},
		openToggle() {
			if(this.shaking) {
				return
			}
			this.capOpen = !this.capOpen
		}
	},
	watch: {
		lock(val) {
			if(val) {
				this.capOpen = false
			}
		}
	},
	onShareAppMessage() {
		return {
			title: '快来找小伙伴一起玩吧！',
			path: '/pages/dice/dice',
			imageUrl: '../../static/share-banner.png'
		}
	}
}
